body {
    background-color: darkgoldenrod;
    margin: 0;
    padding: 0;
}

*:active,
*:focus,
*:hover {
    outline: none !important;
}

.note {
    max-width: 200px;
    min-width: 200px;
    max-height: 200px;
    min-height: 200px;
    background-color: gold;
    position: relative;
    cursor: pointer;
    word-wrap: break-word;
    text-overflow: ellipsis;
}

.btn:focus, .btn:active:focus, .btn.active:focus,
.btn.focus, .btn:active.focus, .btn.active.focus {
    outline: none !important;
    box-shadow: none !important;
}

.note p {
    font-size: unset;
    padding: 5px;
    margin: 5px;
    font-family: Calibri;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    word-wrap: break-word;

}

div.note span {
    position: absolute;
    bottom: 2px;
    right: 2px;
}

.note button {
    margin: 5px;
    visibility: hidden;
}

.note .my-p {
    margin: 5px;
    height: 150px;
    width: 95%;
    overflow: hidden;
    /*position: absolute;*/
}

.note textarea {
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 5px;
    background-color: inherit;
    overflow-x: hidden;
    overflow-y: hidden;
    "?????????"
    -ms-overflow-style: none;
    resize: none;
}

.note textarea::-webkit-scrollbar,
.note textarea::-webkit-scrollbar-corner,
.note textarea::-webkit-scrollbar-button,
.note textarea::-webkit-scrollbar-track,
.note textarea::-webkit-scrollbar-thumb,
.note textarea::-webkit-resizer,
.note textarea::-webkit-scrollbar-track-piece {
    display: none;
    visibility: hidden;
}

.note button:hover, .note button:focus, .note button:active {
    transform: scale(1.5);
    -webkit-transition: all .5s;
    -moz-transition: all .5s;
    -ms-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
}

.my-icon {
    width: 50px;
    height: 50px;
}

.note:focus button, .note:hover button, .note:active button {
    visibility: visible;
}

.board {
    background-color: darkgoldenrod;
}

